@charset "UTF-8";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  margin: 0px;
}

.contenedor-logo-y-nombre__logo-pagina {
  width: 45px;
}

.contenedor-logo-y-nombre__nombre-pagina {
  color: white;
  display: inline-block;
}

.cabecera {
  background-color: rgb(27, 37, 128);
  width: 100%;
  position: sticky;
  padding: 10px;
}
.cabecera .cabecera__menu-navegacion-nav li {
  list-style-type: none;
  font-family: "Staatliches";
}
.cabecera .cabecera__menu-navegacion-nav a {
  text-decoration: none;
  color: white;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera {
  font-size: 14px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
  width: 79%;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa .menu-hamburguesa__enlaceRegistro {
  width: 50px;
  border-radius: 0.5em;
  padding: 6px;
  color: rgb(27, 37, 128);
  background-color: rgb(204, 166, 31);
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__nav-label, .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__nav-input {
  display: none;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .nav-label__icono-hamburguesa {
  width: 30px;
}
.cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__contenedor-logo-y-nombre {
  color: white;
  font-family: "Staatliches";
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3px;
}

@media screen and (min-width: 378px) and (max-width: 520px) {
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__nav-label {
    display: block;
    cursor: pointer;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-flow: column wrap;
    background-color: rgb(204, 166, 31);
    margin-left: 250px;
    margin-right: 20px;
    margin-top: 210px;
    width: 160px;
    height: 250px;
    position: absolute;
    clip-path: circle(0 at center);
    transition: clip-path 1s ease-in-out;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa li a {
    color: black;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa .menu-hamburguesa__enlaceRegistro {
    color: rgb(0, 0, 0);
    background-color: rgb(204, 166, 31);
    border-radius: 0;
    padding: 0;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .nav-label__icono-hamburguesa {
    display: flex;
    top: 0;
    right: 0;
    padding: 5px;
    cursor: pointer;
    background-color: rgb(204, 166, 31);
    position: relative;
    z-index: 1;
  }
  .cabecera .cabecera__menu-navegacion-nav .lista-cabecera__nav-input:checked + .lista-cabecera__menu-hamburguesa {
    clip-path: circle(100% at center);
  }
}
@media screen and (max-width: 378px) {
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera {
    position: relative;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__nav-label {
    display: block;
    cursor: pointer;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa {
    flex-flow: column wrap;
    background-color: rgb(204, 166, 31);
    margin-left: 180px;
    margin-top: 220px;
    width: 130px;
    height: 250px;
    padding-top: 35px;
    padding-bottom: 35px;
    position: fixed;
    clip-path: circle(0 at center);
    transition: clip-path 1s ease-in-out;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa li a {
    color: black;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .lista-cabecera__menu-hamburguesa .menu-hamburguesa__enlaceRegistro {
    color: rgb(0, 0, 0);
    background-color: rgb(204, 166, 31);
    border-radius: 0;
    padding: 0;
  }
  .cabecera .cabecera__menu-navegacion-nav .menu-navegacion-nav__lista-cabecera .nav-label__icono-hamburguesa {
    display: flex;
    top: 0;
    right: 0;
    padding: 5px;
    cursor: pointer;
    background-color: rgb(204, 166, 31);
    position: absolute;
    z-index: 1;
  }
  .cabecera .cabecera__menu-navegacion-nav .lista-cabecera__nav-input:checked + .lista-cabecera__menu-hamburguesa {
    clip-path: circle(100% at center);
  }
}
.pie-pagina {
  background-color: rgb(27, 37, 128);
  position: sticky;
  width: 100%;
  height: 55px;
  padding: 10px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
}
.pie-pagina .pie-pagina__contenedor-redes-sociales {
  justify-self: flex-start;
}
.pie-pagina .pie-pagina__contenedor-redes-sociales a {
  color: #ffffff;
  font-family: "Staatliches";
  font-size: 12px;
}
.pie-pagina .pie-pagina__contenedor-redes-sociales .contenedor-redes-sociales__icono-insta {
  width: 18px;
}
.pie-pagina .pie-pagina__contenedor-redes-sociales .contenedor-redes-sociales__icono_facebook {
  width: 18px;
}
.pie-pagina .pie-pagina__contenedor-redes-sociales .contenedor-redes-sociales__enlace-insta {
  justify-self: flex-start;
}
.pie-pagina .pie-pagina__contenedor-redes-sociales .contenedor-redes-sociales__enlace-facebook {
  justify-self: center;
}
.pie-pagina .pie-pagina__enlace-invierte {
  justify-self: flex-end;
  color: #ffffff;
  font-family: "Staatliches";
  font-size: 12px;
}
.pie-pagina .pie-pagina__enlace-pag-404 {
  color: #ffffff;
  font-family: "Staatliches";
  font-size: 12px;
}

.main-pagina-contacto {
  background-color: rgb(105, 120, 255);
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.main-pagina-contacto .section__formulario-contacto {
  text-align: center;
  padding: 30px;
  /*.flechaAbajo {
    width: 18px;
  }*/
}
.main-pagina-contacto .section__formulario-contacto h2 {
  color: white;
  font-family: "Inter", sans-serif;
  font-size: 18px;
  text-align: center;
  padding-top: 30px;
}
.main-pagina-contacto .section__formulario-contacto h2::after {
  content: "ðŸ‘‡";
}
.main-pagina-contacto .section__formulario-contacto fieldset {
  background-color: rgb(27, 37, 128);
  border: rgb(204, 166, 31);
  width: 550px;
  padding: 10px;
  padding-bottom: 35px;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-nombre {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  padding: 15px;
  padding-top: 30px;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-correo {
  display: flex;
  justify-content: space-evenly;
  flex-direction: row;
  align-items: center;
  gap: 12px;
  padding: 15px;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-duda {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 24px;
  padding: 10px;
  padding-bottom: 30px;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-duda .parrafo-duda__label-duda {
  align-self: start;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-duda textarea {
  background-color: rgb(204, 166, 31);
  width: 220px;
  height: 100px;
  border: rgb(204, 166, 31);
  border-radius: 0.4em;
}
.main-pagina-contacto .section__formulario-contacto fieldset p {
  width: 100%;
}
.main-pagina-contacto .section__formulario-contacto fieldset input {
  background-color: rgb(204, 166, 31);
  border: rgb(204, 166, 31);
  width: 220px;
  height: 30px;
  border-radius: 0.4em;
}
.main-pagina-contacto .section__formulario-contacto fieldset label {
  color: white;
  font-family: "Inter", sans-serif;
  font-size: 16px;
}
.main-pagina-contacto .section__formulario-contacto fieldset #P__boton-enviar {
  border-radius: 1.2em;
  margin-bottom: 25px;
  width: 95px;
  height: 35px;
  padding: 10px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-telefono {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
  padding-bottom: 12px;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-telefono .parrafo-telefono__telefono-contacto {
  color: white;
  text-decoration: none;
  font-family: "Inter", sans-serif;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-telefono span {
  padding-left: 44px;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-telefono .parrafo-telefono__icono-whatsapp {
  width: 18px;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-correo-contacto {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-correo-contacto .parrafo-correo-contacto__email {
  color: white;
  text-decoration: none;
  font-family: "Inter", sans-serif;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-correo-contacto span {
  padding-left: 26px;
}
.main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-correo-contacto .parrafo-correo-contacto__icono-gmail {
  width: 18px;
}

@media screen and (min-width: 400px) and (max-width: 600px) {
  .main-pagina-contacto {
    background-color: rgb(20, 105, 20);
    transition-duration: 0.5s;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset {
    width: 470px;
    transition-duration: 0.5s;
  }
}
