  .main-pagina-contacto {
    background-color: rgb(195, 221, 22);
    transition-duration: 0.5s;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset {
    width: 360px;
    transition-duration: 0.5s;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-nombre {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-nombre .parrafo-nombre__label-nombre {
    padding-bottom: 6px;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-correo {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-duda {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-duda .parrafo-duda__label-duda {
    margin: 0 auto;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-telefono .parrafo-telefono__telefono-contacto span {
    display: block;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-correo-contacto .parrafo-correo-contacto__email span {
    display: block;
  }
}
@media screen and (max-width: 378px) {
  .main-pagina-contacto {
    background-color: aqua;
    transition-duration: 0.5s;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset {
    width: 220px;
    transition-duration: 0.5s;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-nombre {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-nombre .parrafo-nombre__label-nombre {
    padding-bottom: 6px;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-correo {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-duda {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-duda .parrafo-duda__label-duda {
    margin: 0 auto;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-telefono .parrafo-telefono__telefono-contacto span {
    display: block;
  }
  .main-pagina-contacto .section__formulario-contacto fieldset .fieldset__parrafo-correo-contacto .parrafo-correo-contacto__email span {
    display: block;
  }
}